body {
  width: 510px;
  letter-spacing: 5px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  margin: 4% auto;
  font-family: Source Sans Pro, sans-serif;
  font-size: 1.8rem;
}

.calculator {
  background-color: #0e0d0d;
  border-radius: 1px;
  padding: 20px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, .2);
}

.input {
  height: 60px;
  text-align: right;
  color: #bbb;
  border: 1px solid #ddd;
  border-radius: 1px;
  margin-right: 6px;
  padding-top: 10px;
  padding-right: 15px;
  font-size: 2.5rem;
  transition: all .2s ease-in-out;
  overflow-x: auto;
}

.input:hover {
  border: 1px solid #bbb;
  box-shadow: inset 0 1px 4px rgba(0, 0, 0, .2);
}

.operators div {
  width: 80px;
  text-align: center;
  cursor: pointer;
  background-color: orange;
  border: 1px solid #bbb;
  border-radius: 1px;
  margin: 20px 4px 10px 0;
  padding: 10px;
  transition: border-color .2s ease-in-out, background-color .2s, box-shadow .2s;
  display: inline-block;
}

.operators div:hover {
  background-color: rgba(255, 145, 0, .44);
  border-color: #aaa;
  box-shadow: 0 1px 4px rgba(0, 0, 0, .2);
}

.operators div:active {
  font-weight: bold;
}

.leftPanel {
  display: inline-block;
}

.numbers div {
  width: 80px;
  text-align: center;
  cursor: pointer;
  background-color: orange;
  border: 1px solid #111;
  border-radius: 1px;
  margin: 10px 4px 10px 0;
  padding: 10px;
  transition: border-color .2s ease-in-out, background-color .2s, box-shadow .2s;
  display: inline-block;
}

.numbers div:hover {
  background-color: rgba(255, 145, 0, .44);
  border-color: #bbb;
  box-shadow: 0 1px 4px rgba(0, 0, 0, .2);
}

.numbers div:active {
  font-weight: bold;
}

div.equal {
  width: 17%;
  text-align: center;
  vertical-align: top;
  cursor: pointer;
  color: #fff;
  background-color: orange;
  border: orange;
  border-radius: 1px;
  margin: 10px 6px 10px 0;
  padding: 127px 10px;
  transition: all .2s ease-in-out;
  display: inline-block;
}

div.equal:hover {
  background-color: rgba(255, 145, 0, .44);
  border-color: orange;
  box-shadow: 0 1px 4px rgba(0, 0, 0, .2);
}

div.equal:active {
  font-weight: bold;
}

